.App {
  display: grid;
  min-height: 100%;
  grid-template-rows: auto 1fr auto;
}

header {
  background-color: var(--main);
  padding: 0.5rem 1.5rem;
}

.Install-Wrap {
  display: flex;
  justify-content: center;
  font-size: larger;
  margin: 4rem 0 2rem;
}

@media only screen and (min-width: 600px) {
  .Articles {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
}
.Article {
  margin: 2rem;
  max-width: 600px;
}
.Article p {
  text-align: justify;
  line-height: 2rem;
}
.Article-Title {
  text-align: center;
}
